.hero{
    height: 100vh;
    background: linear-gradient(-155deg, #090808, #e1ffea22 60%);
    display: flex;
}
.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
}

.hero-left h2{
    color: #090909;
    font-size: 26px;
    font-weight: 600;
    
}
.hero-left p{
    color: #171717;
    font-size: 100px;
    font-weight: 700;
}
.hero-latest-btn{
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 15px;
    background: #ff4141;
    color: #ffffff;
    font: 22px;
    font-weight: 500;
}

.hero-latest-btn img{
    width: 30px;
}
.hero-right img{
    border-radius: 50px;
    width: 600px;
    height: 700px;
    object-fit: cover;
}

.hero-right{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media(max-width: 1280px){
    .hero-left{
        padding-left: 100px;
    }
    .hero-left h2{
        font-size: 24px;
    }
    .hero-left p{
        font-size: 80px;
    }
    .hero-right img{
        width: 500px;
        height: 600px;
    }
    .hero-latest-btn{
        width: 250px;
        height: 60px;
        font-size: 20px;
    }
}
@media(max-width: 1024px){
    .hero{
        height: 90vh;
    }
    
    .hero-left{
        padding-left: 50px;
    }
    .hero-left h2{
        font-size: 24px;
    }
    .hero-left p{
        font-size: 70px;
    }
    .hero-right img{
        width: 400px;
        height: 500px;
    }
    .hero-latest-btn{
        margin-top: 10px;
        width: 220px;
        height: 50px;
        font-size: 16px;
    }
}
@media(max-width: 800px){
    .hero{
        height: 80vh;
    }
    .hero-left{
        padding-left: 20px;
    }
    .hero-left h2{
        font-size: 20px;
    }
    .hero-left p{
        font-size: 50px;
    }
    .hero-right img{
        width: 300px;
        height: 400px;
    }
    .hero-latest-btn{
        margin-top: 5px;
        width: 200px;
        height: 40px;
        font-size: 14px;
    }
}
@media(max-width: 500px){
    .hero{
        height: 60vh;
    }
    .hero-left{
        padding-left: 5px;
    }
    .hero-left h2{
        font-size: 16px;
    }
    .hero-left p{
        font-size: 28px;
    }
    .hero-right img{
        width: 200px;
        height: 300px;
    }
    .hero-latest-btn{
        width: 150px;
        height: 30px;
        font-size: 12px;
    }
    .hero-latest-btn img{
        width: 20px;
    }   
}
