.cartitems{
    margin: 100px 170px;
}
.cartitems hr{
    height: 3px;
    background: #e2e2e2;
    border: 0px;
}
.cartitems-format-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}
.cartitems-format{
    font-size: 17px;
    font-weight: 500;

}
.carticon-product-icon{
    height: 62px;
}
.carticon-remove-icon{
    width: 15px;
    margin: 0px 40px;
    cursor: pointer;
}
.carticons-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #fff;   
}
.carticons-down{
    display: flex;
    margin: 100px 0px;
}
.carticons-total{
    flex-direction: column;
    display: flex;
    flex: 1;
    margin-right: 200px;
    gap:40px;
    
}
.carticons-total-item{
    display: flex;
    justify-content: space-between;
    padding: 15px 0px;
}
.carticons-total button{
    width: 262px;
    height: 58px;
    outline: none;
    border: none;
    background: black;
    color:#fff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}
.articons-promocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}
.articons-promocode p{
    color:#555;

}
.carticons-promo-box{
    width: 504px;
    margin-top: 15px;
    padding-left: 20px;
    height: 58px;
    background:#eaeaea;
}
.carticons-promo-box input{
    border: none;
    outline: none;
    background: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
}
.carticons-promo-box button{
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: black;
    color: white;
    cursor: pointer;
}
@media(max-width: 1280px){
    .cartitems{
        margin: 100px 100px;
    }
    .cartitems-format-main{
        gap: 50px;
    }
    .cartitems-format{
        font-size: 16px;
    }
    .carticon-remove-icon{
        margin: 0px 20px;
    }
    .carticons-total{
        margin-right: 100px;
    }
    .carticons-total-item{
        padding: 10px 0px;
    }
    .carticons-total button{
        width: 200px;
        height: 50px;
        font-size: 14px;
    }
    .carticons-promo-box{
        width: 400px;
    }
    .carticons-promo-box input{
        width: 250px;
        height: 40px;
    }
    .carticons-promo-box button{
        width: 130px;
        height: 40px;
        font-size: 14px;
    }
}
