.productdisplay{
    display: flex;
    margin: 0px 170px;
    padding-left: 100px;
}
.productdisplay-left{
    display: flex;
    gap: 17px;
}
.productdisplay-img-list{
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.productdisplay-img-list img{
    height: 163px;
}
.productdisplay-main-img{
    width: 586px;
    height: 700px;
    object-fit: cover;
}
.productdisplay-right{
    margin: 0px 70px;
    display: flex;
    flex-direction: column;
}
.productdisplay-right h1{
    color: #3d3d3d;
    font-size: 40px;
    font-weight: 700;

}
.productdisplay-right-star{
    display: flex;
    align-items: center;
    margin: 13px 0px;
    gap: 5px;
    color: #1c1c1c;
    font-size: 16px;
}
.productdisplay-right-star img{
    width: 20px;
 }

.productdisplay-right-prices{
    display: flex;
    gap: 30px;
    margin: 40px 0px;
    font-size: 24px;
    font-weight: 700;
}

.productdisplay-right-feature h1{
    margin-top: 10px;
    color:#656565;
    font-size: 20px;
    font-weight: 600;
}
.productdisplay-right-options{
    display: flex;
    gap: 20px;
    margin: 10px 0px;
}
.productdisplay-right-options div{
    padding: 18px 24px;
    background: #fbfbfb;
    display: flex;
    align-items: center;
    border-radius: 50px;
    border: 1px solid #ebebeb;
    cursor: pointer;
}
.productdisplay-right button{
    padding: 20px 40px;
    width: 200px;
    border: none;
    background: black;
    color: white;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 40px;
    outline: none;
    cursor: pointer;
}
.productdisplay-right-category{
    margin-top: 10px;

}
.productdisplay-right-category span{
    font-weight: 600;
}
.option:hover {
    background-color: #f0f0f0;
}

.option.selected {
    background-color: black;
    color: white;
}
@media(max-width: 1280px){
    .productdisplay{
        padding-left: 50px;
        margin: 0px 60px;
    }
    .productdisplay-left{
        gap: 10px;
    }
    .productdisplay-img-list{
        gap: 10px;
    }
    .productdisplay-img-list img{
        height: 120px;
    }
    .productdisplay-main-img{
        width: auto;
        height: 510px;
    }
    .productdisplay-right{
        margin: 0px 30px;
    }
    .productdisplay-right h1{
        font-size: 22px;
    }
    .productdisplay-right-star{
        gap: 3px;
        font-size: 13px;
    }
    .productdisplay-right-prices{
        margin: 10px 0px;
        font-size: 18px;
    }
    .productdisplay-right-description{
        font-size: 13px;
    }
    .productdisplay-right-feature h1{
        margin-top: 20px;
        font-size: 20px;
    }
    .productdisplay-right-options div{
        padding: 14px 20px;
    }
    .productdisplay-right button{
        width: 150px;
        padding: 15px 0px;
        /* font-size: 14px; */
        margin-bottom: 20px;
    }
    .productdisplay-right-category{
        margin-top:5px;
        font-size: 14px;
    }

}
@media(max-width: 1024px){
    .productdisplay{
        padding-left: 30px;
        margin: 0px 30px;
    }
    .productdisplay-left{
        gap: 5px;
    }
    .productdisplay-img-list{
        gap: 5px;
    }
    .productdisplay-img-list img{
        height: 80px;
    }
    .productdisplay-main-img{
        width: auto;
        height: 350px;
    }
    .productdisplay-right h1{
        font-size: 18px;
    }
    .productdisplay-right-star img{
        width: 15px;
    }
    .productdisplay-right-description{
        font-size: 12px;
    }
    .productdisplay-right-feature h1{
        margin-top: 20px;
        font-size: 20px;
    }
    .productdisplay-right-options{
        margin: 10px 0px;
    }
    .productdisplay-right-options div{
        padding: 10px 16px;
        font-size: 12px;
    }
    .productdisplay-right button{
        width: 120px;
        padding: 10px 0px;
        margin-bottom: 10px;
        font-size: 14px;
    }
    .productdisplay-right-category{
        font-size: 12px;
    }

}
@media(max-width: 800px){
    .productdisplay{
        padding-left: 0px;
        margin: 0px 10px;
    }
    .productdisplay-left{
        gap: 0px;
        flex-direction: column-reverse;
        align-items: center;
    }
    .productdisplay-img-list{
        flex-direction: row;
        gap: 6px;
    }
    .productdisplay-img-list img{
        height: 70px;
    }
    .productdisplay-main-img{
        width: auto;
        height: 300px;
    }
    .productdisplay-right h1{
        font-size: 14px;
    }
    .productdisplay-right-star img{
        width: 13px;
    }
    .productdisplay-right-description{
        font-size: 10px;
    }
    .productdisplay-right-feature h1{
        margin-top: 10px;
        font-size: 14px;
    }
    .productdisplay-right-options{
        margin: 10px;
        gap: 5px;
    }
    .productdisplay-right-options div{
        padding: 4px 11px;
    }
    .productdisplay-right button{
        width: 100px;
        font-size: 12px;
    }
    .productdisplay-right-category{
        font-size: 10px;
    }
}

@media(max-width: 500px){
    .productdisplay{
        flex-direction: column;

    }
    .productdisplay-left{
        gap: 10px;
        flex-direction: row;
        margin: auto;
    }
    .productdisplay-img-list{
        flex-direction: column;
        gap: 8px;
    }
    .productdisplay-img-list img{
        height: 75px;
        width: 80px;
    }
    .productdisplay-main-img{
        width: 300px;
        height: 330px;
    }
    .productdisplay-right{
        margin: 5px;
    }
    .productdisplay-right h1{
        margin-top: 15px;
        font-size: 20px;
        font-weight: 500;
    }
    .productdisplay-right-star img{
        width: 15px;
    }
    .productdisplay-right-options{
        gap:10px;
        margin: 20px 0px;
    }
    .productdisplay-right-options div{
        padding: 10px 16px;
        font-size: 14px;
    }
    .productdisplay-right button{
        width: 130px;
        padding: 10px 0px;
        font-size: 15px;
    }
    .productdisplay-right-category{
        font-size: 16px;
    }

}